import s from './User.module.css';
import userImg from './../../../image/photo.jpg';
import {NavLink} from "react-router-dom";
import { CgArrowsScrollH} from 'react-icons/cg';


const User = (props) => {
    return (
        <div className={s.user}>
            <div className={s.img_button}>
                <NavLink to={'/profile/' + props.id}>
                        <span className={''}>
                            <img src={props.photo !== null ? props.photo : userImg} alt={'userPhoto'}/>
                        </span>
                </NavLink>
                <div>
                    {props.followed
                        ? <button disabled={props.followingInProgress.some(id => id === props.id)}
                                  className={s.button} onClick={(e) => {
                            props.unFollow(props.id)
                        }}> Follow</button>
                        : <button disabled={props.followingInProgress.some(id => id === props.id)}
                                  className={`${s.button} ${s.color}`} onClick={(e) => {
                            props.follow(props.id);
                        }}>unFollow</button>}
                </div>
            </div>
            <div className={s.userIcons}>
                 <CgArrowsScrollH/>
            </div>
            <div className={s.aboutUser}>
                <div className={s.name_status}>
                    <div className={s.fullName}>
                        {props.name}
                    </div>
                    <div className={s.status}>
                        {props.status || '......'}
                    </div>
                </div>
                <div>
                    <div className={s.country}>
                        {'props.country'}
                    </div>
                    <div>
                        {'props.sity'}
                    </div>
                </div>
            </div>
        </div>
    )
};

export default User;