import s from './Users.module.css';
import User from "./User/User";
import Preloader from "../common/preloader/Preloader";
import Paginator from '../common/Paginator/Paginator';


const Users = (props) => {
    
    return <div className={s.mainDiv}>
        <p>Users</p>
        <Paginator {...props} portionSize={5} />
        {props.isFetching ? <Preloader uniqueClassName={'UserPreloader'}/> : null}
        {props.users.map(u =>
            <User key={u.id} photo={u.photos.small} followed={u.followed} id={u.id} name={u.name}
                  status={u.status} unFollow={props.unFollow} follow={props.follow}
                  followingInProgress={props.followingInProgress}/>)}
    </div>
};

export default Users;


