import { useState } from 'react';
import s from './Paginator.module.css';
import cn from "classnames";


const Paginator = ({totalItemsCount, pageSize, currentPage, onChangePage, portionSize}) => {

    const pagesCount = Math.ceil(totalItemsCount / pageSize);
    const pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    const portionCount = Math.ceil( pagesCount / portionSize);
    const [portionNumber, setPortionNumber] = useState(1);
    const leftPortionPart = (portionNumber - 1) * portionSize + 1;
    const rightPortionPart = portionNumber * portionSize;
    const lostPage = (portionCount * portionSize - 4);
    
    return (
        <div className={s.paginator}>
            {<button className={s.button} disabled={!(portionNumber > 1)} onClick={()=>{
                setPortionNumber(portionNumber - 1)
            }}>&laquo;</button>}
        {pages
        .filter(p => p >= leftPortionPart && p <= rightPortionPart)
        .map(p => {
            return <span key={p} className={cn({[s.selected]: currentPage === p}, s.page)}
                         onClick={(e) => {
                             onChangePage(p)
                         }}>
                    {p}
                </span>;
        })}
        <span className={s.points}>.....</span>
        <span className={cn({[s.selected]: currentPage === lostPage}, s.page)}   onClick={(e) => {
                             onChangePage(lostPage)
                         }}>{lostPage}</span>
          {portionCount > portionNumber && <button className={s.button} onClick={()=>{
                setPortionNumber(portionNumber + 1)
            }}>&raquo;</button>}
    </div>
    )
}

export default Paginator;